/* Artist profile design */

.artist-profile-container {
    display: block;
    padding-bottom: 20px;
    transition: 1000ms;
}

.artist-profile {
    padding: 10px 15% 10px;
    background-color: #636363;
    border-radius: 0px 0px 10px 10px;
    height: 100%;
    transition: 1000ms;
}

.artist-profile-main-info {
    flex: 0 0 70%;
}

.opened {
    display: flex;
    padding: 10px 80px 10px;
    transition: 1000ms;
}

.artist-profile-pic,
.artist-info-name,
.artist-info-bio,
.artist-info-followers,
.tracks-label {
    margin-top: 10px;
}

.tracks-label {
    margin-bottom: 10px;
}

img {
    width: 20px;
    height: 20px;
}

img.vertical-center {
    width: 130px;
    height: 130px;
    vertical-align: middle;
}

/* Product view container */
ul {
    margin: 0;
    padding: 0; 
 }

.product-view {
    display: block;
    background-color: #363636;
    border-style: solid;
    border-color: #797979;
    font-size: x-small;
    margin-bottom: 20px;
}

.product-view-container {
    height: 180px;
    display: flex;
    justify-content:flex-start;
}

.product-img {
    margin: 5px 5px;
    position: relative;
}

.vertical-center {
    margin: 0;
    position: relative;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

.product-content {
    width: 100%;
    height: 155px;
}

.product-title {
    margin: 5px;
}

table.product-tracks {
    display: block;
    margin: 5px 5px 0px 5px;
    table-layout: auto;
    border-collapse: separate;
    border-spacing: 3px 5px;
}

.product-tracks tbody {
    display: inherit;
    overflow: auto;
    height: 130px;
}

tr.product-track {
    display: flex;
    height: 30px;
    margin: 0;
}

td.track-play-button {
    display: flex;
    justify-content: space-between;
    margin-right: 5px;
    width: 60px;    
}

td.track-title {
    width: 75%;
}

td.track-playcount {
    text-align: center;
    width: 8%;
}

