.product-marketing-info {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 10px;
}

.product-price, .product-likes {
    display: inherit;
}

.product-price {
    margin-left: 10px;
}

.product-price-wrapper {
    display: inherit;
}

.product-likes, .icon-likes {
    margin-right: 10px;
}


/* Custom scroll */

/* width */
::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}
  
/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
