/*-------------------------------------------------- */

/* Provide sufficient contrast against white background */
a {
    color: #ffffffc4 !important;
}

.btn-primary {
    color: #fff;
    background-color: #1b6ec2;
    border-color: #1861ac;
}

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
    color: #fff;
    background-color: #1b6ec2;
    border-color: #1861ac;
}

/* Navbar styles
-------------------------------------------------- */
.navbar {
    height: 80px;
}

.navbar-dark {
    background-color: hsla(0, 0%, 77%, 0.1);
}

.nav-link {
    font-size: 14px;
}

.nav-item {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar-nav > li {
    padding-left: 30px;
    padding-right: 30px;
}

a.navbar-brand {
    white-space: normal;
    text-align: center;
    word-break: break-all;
    font-size: 16px;
}
