/* Images */

.img-small {
    width:50px;
    height:50px;
}

.img-big {
    width:250px;
    height:250px;
    border: 5px solid #555;
}

.cart-total-quantity {
    font-size: 12px;
}


.welcome-message {
    padding: 100px;
}

.view-title {
    margin: 10px 5px;
}

p {
    margin-top: 10px;
    margin-bottom: 10px;
}
