.filter-list {
    display: flex;
    flex-direction: row;
    flex-grow: 2;
    flex-wrap: wrap;
    justify-content: center;
    vertical-align: middle;
    padding: 6px;
    margin-bottom: 20px;
    font-size: small;
    background-color: gray;
    border-radius: 5px;
}

.filter {
    display: inherit;
    justify-content: inherit;
    align-items: center;
    margin: 0 10px;
}