.follow-button {
    --t:2px;   /* Thickness */
    --l:40px;  /* size of the symbol */
    --s:10px;  /* space around the symbol */
    --c1:#fff; /* Plus color*/
    --c2:rgb(92, 92, 92); /* background color*/
    text-align: center;
    display:inline-block;
    width:var(--l);
    height:var(--l);
    padding:var(--s);
    box-sizing:border-box; /*Remove this if you don't want space to be included in the size*/
    
    background:
      linear-gradient(var(--c1),var(--c1)) content-box,
      linear-gradient(var(--c1),var(--c1)) content-box,
      var(--c2);
    background-position:center;
    background-size: 100% var(--t),var(--t) 100%;
    background-repeat:no-repeat;
    transition: 100ms;
  }
  
.radius {
  border-radius:50%;
}

.following {
  --c2: green;
  background-position: unset !important;
  transition: 100ms;
}

.remove {
  background-color: red !important;
}
