/* Player design */

.player-container {
    padding: 5px 10px 5px;
    width: 100%;
    display: flex;
    flex-direction: row;
    font-size: 11px;
    display: block;
    background-color: grey;
    border-radius: 10px;
    margin-bottom: 20px;
}
#artist-tracks {
    display: block;
}

label {
    margin: 2px !important;
}


.track-info-value {
    margin-left: 10px;
}

audio {
    margin-top: 5px;
    height: 20px;
    width: 100%;
}