.comment-section-container {
    position: sticky;
    top: 20px;
    background-color: #7d7d7e;
    height: 700px;
    width: 320px;
    margin: 20px;
    padding: 10px;
    opacity: 0;
    transition: 1000ms;
    border-radius: 5px;
    overflow-y: auto;
    overflow-x: hidden;
}

.show {
    opacity: 100;
    transition: 1000ms;
}

.addtxt {
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
    font-size: 13px;
    width: 350px;
    background-color: #969696;
    font-weight: 500
}

.form-control:focus {
    color: #000
}

.second {
    width: 350px;
    background-color: white;
    border-radius: 4px;
}

.text1 {
    font-size: 13px;
    font-weight: 500;
    color: #56575b
}

.text2 {
    font-size: 12px;
    font-weight: 500;
    margin-left: 6px;
    color: #56575b
}

.text3 {
    font-size: 11px;
    font-weight: 500;
    margin-right: 4px;
    color: #828386
}

.text3o {
    color: #00a5f4
}

.text4 {
